@import "_variables.scss";

//===== GENERAL =====//
body {
    font-family: 'Roboto', sans-serif;
    font-size: 16px;
    font-weight: 400;
    color: $color-primary;

    background-color: $color-bg;

    overflow-x: hidden;
}

a {
    color: $color-primary;
}

.visually_hidden {
    position: absolute;
    width: 1px;
    height: 1px;
    margin: -1px;

    border: 0;
    padding: 0;

    white-space: nowrap;
    clip-path: inset(100%);
    clip: rect(0 0 0 0);
    overflow: hidden;

}

.lock {
    overflow: hidden;
}